import React from "react";
import styled from 'styled-components';
import {Icon} from "../icon";
import {Article, Title, IconBox} from "../styled";

const LanguageItem = styled.span`
  display: block;
  margin-bottom: .5rem;
`;

const Languages = () => {
  const data = [
    {
      id: 1,
      language: 'Russian - Fluent'
    },
    {
      id: 2,
      language: 'Romanian - Intermediate'
    },
    {
      id: 3,
      language: 'English - Basic'
    }
  ];

  return (
    <Article>
      <Title>
        <IconBox>
          <Icon name="language" />
        </IconBox>
        Languages
      </Title>
      {data.map((item) => (
        <LanguageItem key={item.id}>{item.language}</LanguageItem>
      ))}
    </Article>
  );
};

export default Languages;
