import {createGlobalStyle} from 'styled-components'

const globalStyle = createGlobalStyle`
  /* Box sizing rules */
  *,
  *::before,
  *::after {
    box-sizing: border-box;
  }
  
  /* Remove default margin */
  body,
  h1,
  h2,
  h3,
  h4,
  ul[class],
  ol[class],
  li,
  figure,
  figcaption,
  blockquote,
  dl,
  dd {
    margin: 0;
  }

  /* Set core body defaults */
  body {
    min-height: 100vh;
    font-family: 'Lato', sans-serif;
    font-size: 100%;
    line-height: 1.5;
    color: ${(props => props.theme.colors.black)};
    scroll-behavior: smooth;
    text-rendering: optimizeSpeed;
  }
  
  a {
  
    &:not([class]) {
      color: ${(props => props.theme.colors.blue)};
      text-decoration-skip-ink: auto;
      text-decoration: none;
    }
    
    &:focus,
    &:visited,
    &:active,
    &:focus-within {
      color: ${(props => props.theme.colors.blue)};
    }
    
    &:focus {
      outline: none;
    }
  }

  /* Remove list styles on ul, ol elements with a class attribute and remove default padding */
  ul,
  ol {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  /* Make images easier to work with */
  img {
    max-width: 100%;
    display: block;
  }

  /* Inherit fonts for inputs and buttons */
  input,
  button,
  textarea,
  select {
    font: inherit;
  }
  
  h1 {
    font-size: 1.5rem;
  }
  
  h2 {
    font-size: 1.25rem;
  }

  h3 {
    font-size: 1.125rem;
    margin-bottom: 15px;
  }
  
  h4 {
    font-size: 1rem;
    margin-bottom: 15px;
  }
  
  p {
    margin: 0 0 .5rem;
  }
  
  small {
    font-size: .875rem;
  }

  /* Remove all animations and transitions for people that prefer not to see them */
  @media (prefers-reduced-motion: reduce) {
    * {
      animation-duration: 0.01ms !important;
      animation-iteration-count: 1 !important;
      transition-duration: 0.01ms !important;
      scroll-behavior: auto !important;
    }
  }
  
  /* Large media */
  @media (min-width: 992px) {
  
    body {
      font-size: 100%;
    }
  
    h1 {
      font-size: 2rem;
    }
  
    h2 {
      font-size: 1.75rem;
    }
  
    h3 {
      font-size: 1.5rem;
    }
  
    h4 {
      font-size: 1.25rem;
    }
  
    small {
      font-size: .875rem;
    }
  }

  /* The hover CSS media feature can be used to test whether the user's primary input mechanism can hover over elements */
  @media (hover: hover) {
    a:not([class]):hover {
      text-decoration: underline;
    }
  }
`;
export default globalStyle;
